<template>
  <div>
    <mt-header fixed title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="header qrboard">
      <div class="qrboard-container">
       <!-- <qrcode-stream
          @decode="onDecode"
          :camera="camera"
          :paused="paused"
          @init="onCameraChange"
        ></qrcode-stream> -->
        <qrcode-stream
          @init="onCameraChange"
        ></qrcode-stream>
      </div>
      <p>QR Code</p>
    </div>
  </div>
</template>

<script>
import { MessageBox } from "mint-ui";
import { QrcodeReader,Qrcodestream } from 'vue-qrcode-reader';
import TokenManager from "@/configs/token-manager";
export default {
  name: "recognizeQR",
  components: { QrcodeReader, Qrcodestream },
  data() {
    return {
      cameraSettings: {
        audio: false,
        video: {
          facingMode: { ideal: 'environment' }
        }
      },
      camera: 'auto',
      paused: false,
      content:""
    }
  },
  created() {
    if (!TokenManager().accessToken) {
      MessageBox.alert("Token ", "").then(action => {
        this.$router.push("/");
      });
    }
  },
  methods: {
    // async onDecode (content) {
    //   try {
    //     this.content = content;
    //     this.pauseCamera(); // 
  
    //     //  redeem 
    //     let message = await this.redeem(content);
    //     // 
    //     Swal('Good job!',
    //           message,
    //           'success').then(() => {
    //             this.unPauseCamera()
    //     });
    //   } catch (error) {
    //       Swal('Whoops!',
    //             error.message,
    //             'error').then(() => {
    //             this.unPauseCamera()
    //       });
    //   }
    // },
    // pauseCamera () {
    //   this.paused = true
    // },
    // unPauseCamera () {
    //   this.paused = false
    // },
    // redeem (content) {
    //  return new Promise((resolve, reject) => {
    //       // 
    //     if (content) { 
    //       resolve('Success'); 
    //     } else { 
    //       reject('failed'); 
    //     }
    //   });
    // },
    onCameraChange (promise) {
        const getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;
       alert(getUserMedia);
      promise.catch(error => {
        const cameraMissingError = error.name === 'OverconstrainedError'
        const triedFrontCamera = this.camera === 'front'
        
        if (triedFrontCamera && cameraMissingError) {
           MessageBox.alert(" Camera", "").then(action => {
            this.$router.push("/");
          });
        }
      })
   }
  }
};
</script>
<style scoped>
.qrboard {
  background: black;
  padding: 25% 10px;
  height: calc(100vh - 50px);
}
.qrboard-container {
  border: 0.25em solid #4679eb;
  position: relative;
}
.qrboard-container::before,
.qrboard-container::after {
  content: "";
  display: block;
  position: absolute;
  background: #000;
}

.qrboard-container::before {
  top: -0.3em;
  bottom: -0.3em;
  left: 1em;
  right: 1em;
}

.qrboard-container::after {
  left: -0.3em;
  right: -0.3em;
  top: 1em;
  bottom: 1em;
}
.qrboard p {
  text-align: center;
}
.wrapper {
  height: 280px;
  z-index: 1!important;
}
</style>



